import React from 'react'
import { Toitem } from "./Toitem";

export const Todos = (props) => {
    let sty ={
        minHeight:"80vh" 

    }
    return (
        <div className="container" style= {sty}>
            <h3 className="text-center my-5" >Todos List</h3>
           {props.todos.length===0? "!!No Todos!!" :
             props.todos.map((todo)=>{
              return <Toitem todo={todo} key={todo.sno} onDelete={props.onDelete}/>
            })
        
        }
           
        </div>
    )
}
